.body {
  width: 100%;
  margin: 0;
  position: relative;
}

.text {
  color: #f5f5f5;
  text-align: center;
  width: fit-content;
  margin: 1em auto;
  padding: .5em;
  font-family: Franklin Gothic Medium, Arial Narrow, Arial, sans-serif;
  font-size: 1.2rem;
}

#welcome {
  color: var(--font-color-1);
  text-align: center;
